*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --container-padding: 20px;
  --container-padding-tablet: 32px;
  --items: 1;
  --indent: 16px;
  --color-primary: #f87719;
  --color-white: #fff;
  --color-black: #000;
  --color-dark-grey-1: #111;
  --color-dark-grey-2: #282828;
  --color-dark-grey-3: #1c1c1c;
  --color-grey-1: #595959;
  --color-grey-2: #b7b7b7;
  --color-light-grey-1: #f8f8f8;
  --linear-gradient-1: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --linear-gradient-2: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition-duration: .25s;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.weekly-trends__link, .header__menu-link, .header__menu-toggle, .header__title, .header__logo {
  color: currentColor;
  text-decoration: none;
}

.upcoming__list-info, .card__rate--list, .header__menu {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

.modal__cloze-button, .button {
  cursor: pointer;
  padding: 0;
  font-family: inherit;
}

.visually-hidden:not(:focus):not(:active) {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Medium), local(Roboto-Medium), url("Roboto-Medium.00ce43a4.woff2") format("woff2"), url("Roboto-Medium.20f08e1a.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto), local(Roboto-Regular), url("Roboto-Regular.d2ce89af.woff2") format("woff2"), url("Roboto-Regular.f6e06c71.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Bold), local(Roboto-Bold), url("Roboto-Bold.b5f912e3.woff2") format("woff2"), url("Roboto-Bold.4e7b4341.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

