.dark {
  background-color: var(--color-black);
  color: var(--color-white);
}

.card-set {
  gap: var(--indent);
  flex-wrap: wrap;
  display: flex;
}

.card-set__item {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1)) / var(--items));
}

